//button
.heading {
  font-family: Be Vietnam, sans-serif;
  text-align: center;
  &__h1 {
    font-size: 2.5rem;
    text-transform: none; }
  &__h2 {
    margin: 1rem 0 1.5rem; }
  &__h3 { //titles in functions section
    font-family: $font-primary;
    text-align: left;
    margin: .5rem 0;
    &--calculator {
      margin-top: 20px; } }
  &__h4 {
    font-size: 1.1rem; } }
//footer - authors names
.btn {
  max-width: 240px;
  width: 140px;
  height: 50px;
  padding: .5rem 1rem;
  margin: 1rem 0;
  color: #fff;
  border-radius: 0.5rem;
  font-size: 1.2rem;
  text-transform: uppercase;
  background: #1976D2;
  border: none;
  &:focus {
    outline: none; }
  &:hover {
    background: $primary-color; }
  &__container {
    display: flex;
    justify-content: center;
    &-hero {
      order: -1;
      margin: 2rem;
 } }      //padding: 2rem 0 0 0
  &-hero {
    background-color: $primary-color-dark;
    &:hover {
      background-color: $primary-color; } } }
.icon__container {
  font-size: 1.1rem;
  & > i {
    display: inline-block;
    text-align: center; } }
